<template>
  <div class="memberhome">
    <layout-member :active="1">
      <h2 class="avatar"><img :src="currentuser.avatar"></h2>
      <div class="des">
        <transition name="list" tag="div">
          <div v-html="currentuser.des"></div>
        </transition>
      </div>
    </layout-member>
  </div>
</template>
<script>
  import LayoutMember from '../modules/layout_member.vue'
  import {userdata} from '../mock-data/userdata.js'
  export default {
    components:{
      LayoutMember,
    },
    data(){
      return{
        currentuser:{
          "name":"",
          "job":"",
          "avatar":"",
          "des":"",
        },
        userdatas:userdata
      }
    },
    created(){
      var name = this.$route.params.user;
      if(name&&this.userdatas&&this.userdatas[name]){
        this.currentuser = this.userdatas[name];
      }
    }
  }
</script>

<style lang="less">
.list-item {
  display: inline-block;
  margin-right: 10px;
}
.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-active {
  opacity: 0;
  transform: translateY(30px);
}
.memberhome{
  .avatar{
    padding:100px 0 50px 0;
  }
  .des{
    line-height: 20px;
    color: #c1c1c1;
    font-weight: 100;
    font-size: 12px;
  }
}


@media screen and (min-width: 320px) and (max-width: 768px) {
  .memberhome .avatar{
    float: right;
    padding-right:30px;
  }
  .memberhome .des{
    clear:both;
    div{
        padding: 0 30px 30px 30px;
    }
  }

}










</style>



